.title-container {
  position: relative;
  margin-bottom: 10px;
  display: inline-block; }
  .title-container .title {
    font-size: 28px; }
  .title-container .background {
    position: absolute;
    top: 2px;
    right: 0;
    width: 100%;
    height: 30px;
    background: rgba(3, 174, 239, 0.3);
    transform: skew(20deg); }

.attribution {
  font-size: 12px;
  color: #666;
  margin-bottom: 20px; }

.description {
  font-size: 16px;
  line-height: 24px; }

.image-list {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center; }
  .image-list img {
    width: 300px;
    margin-bottom: 30px;
    box-shadow: 10px 10px 20px #333333;
    cursor: zoom-in; }

.lightbox-image-container {
  width: 50%;
  height: 90vh;
  display: flex;
  justify-content: center; }
  .lightbox-image-container .lightbox-image {
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center; }
